@import "../../assets/scss/app";

.header {
  display: flex;
  justify-content: space-between;
  padding-top: vw(22);

  @include tablet() {
    padding-top: 22px;
  }

  @include desktop() {
    padding-top: 43px;
  }

  .logoBlock {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 200;

    & > svg {
      //margin-left: vw(20);
      margin-top: vw(12);
      //width: vw(195);
      width: vw(240);

      @include tablet() {
        //margin-left: 20px;
        margin-top: 12px;
        //width: 195px;
        width: 240px;
      }

      @include desktop() {
        //margin-left: 20px;
        margin-top: 0;
        //width: 285px;
        width: 350px;
      }

    }
  }

  .rightBlock {

    @include desktop {
      margin-top: 11px;
    }

    .topRow {
      display: flex;
      flex-direction: column;

      @include desktop() {
        flex-direction: row;
        align-items: center;
      }

      .topRowLeft {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }

      .topRowRight {
        display: flex;
        align-items: center;
        margin-top: vw(14);

        @include tablet() {
          margin-top: 14px;
        }

        @include desktop() {
          margin-top: 0;
        }

        .langSwitcher {
          @include desktop() {
            margin-left: 150px;
          }
        }

        .socialWrapper {
          position: relative;
          z-index: 200;
          margin-left: vw(16);

          @include tablet() {
            margin-left: 16px;
          }

          @include desktop() {
            margin-left: 150px;
          }

        }

        .tel {
          @include onlyDesktop(flex) {
            margin-left: 81px;
          }
        }
      }
    }

    .BottomRow {
      @include onlyDesktop(flex) {
        margin-top: 30px;
      }
    }

  }
}